@import "../../global.scss";

.contact {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
  }

  .left {
    flex: 1;
    img {
      height: 90%;
      overflow: hidden;
    }
  }
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h2 {
      font-size: 30px;
    }
    form {
      width: 70%;
      height: 370px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      @include mobile {
        height: 270px;
      }

      input {
        width: 300px;
        height: 30px;
        font-size: 14px;
        @include mobile {
          width: 200px;
          height: 40px;
        }
      }
      textarea {
        width: 300px;
        height: 200px;
        font-size: 14px;
        @include mobile {
          width: 200px;
          height: 130px;
        }
      }

      button {
        width: 150px;
        height: 30px;
        color: white;
        background-color: crimson;
        border: none;
        border-radius: 10px;
        font-weight: 500;
        cursor: pointer;
        @include mobile {
          height: 40px;
        }

        &:focus {
          outline: none;
        }
      }
      span {
        color: crimson;
        font-weight: 500;
      }
    }
  }
}
